	module.exports = {"data":{"markdownRemark":{"html":"<h1>***</h1>\n<section>\n<p>Куда ни глянь,<br>\nодна полынь.<br>\nКак будто янь<br>\nвпитала инь.</p>\n<p>Куда ни глянь,<br>\nодин и тот<br>\nже сон — и я<br>\nна нем затёрт<br>\nсмешным деньком,<br>\nгде все поют<br>\nпро чудный дом<br>\nи про семью.<br>\nА я? Что я?<br>\nКуда мне встать?<br>\nНа задний ряд?<br>\nНельзя моргать.<br>\nНельзя моргать.<br>\nНельзя моргать.<br>\nНе загораживать<br>\nлюдей.</p>\n<p>Нельзя моргать.<br>\nНельзя моргать.<br>\nСмотреть перед собой<br>\nи петь<br>\nпро чудный дом<br>\nи про семью,<br>\nв которой я<br>\nне поселюсь.<br>\nСтоять в ряду,<br>\nсмотреть прямей — <br>\nвсе вспышку ждут,<br>\nа что за ней?<br>\nА кто из нас останется?<br>\nНельзя моргать.<br>\nНельзя моргать.<br>\nНельзя избавиться.</p>\n<p>Нельзя моргать.<br>\nНельзя моргать.<br>\nСтою обезоруженный.<br>\nКуда ни глянь,<br>\nвсё будет так,<br>\nкак будто бы заслужено.<br>\nНельзя моргать.<br>\nНельзя моргать.<br>\nНельзя избавиться.<br>\nВсё будет так.<br>\nВсё будет так,<br>\nкак будто это нравится.</p>\n<p>Сухая ткань<br>\nбыстрее глаз<br>\nвпитает нас.<br>\nВпитает нас.</p>\n</section>"}},"pathContext":{"slug":"lyrics/bezImeni2"}}

